import tabControl from '@/components/tabControl-tag/tabControl-tag.vue';
export default {
	components: {
		tabControl
	},
	data() {
		return {
			current: 0,
			items: ['排队中', '已接待', '已取消'],
			a: [],
			b: [],
			c: [],
			paidui: 0,
			shopid: 0
		}
	},
	onLoad(e) {
		this.shopid = e.shopid

	},
	onShow() {
		var that = this
		this.pageInit()
	},
	methods: {
		toDetail(id) {
			uni.navigateTo({
				url: "../paiduiorderDetails/paiduiorderDetails?id="+id
			})
		},
		onClickItem: function(val) {
			this.current = val.currentIndex
		},
		pageInit() {
			var that = this
			that.a = []
			that.b = []
			that.c = []
			uni.request({
				url: this.$bashUrl + '/index.php/home/Api/store_line/',
				method: 'POST',
				data: {
					id: this.shopid,
				},
				success: (res => {
					console.log(res)
					for (var i = 0; i < res.data.length; i++) {
						if (res.data[i].state == 0) {
							that.a.push(res.data[i])
						} else if (res.data[i].state == 1) {
							that.b.push(res.data[i])
						} else if (res.data[i].state == 2) {
							that.c.push(res.data[i])
						}
					}
					that.paidui = that.a.length
				}),
				fail: (err => {
					console.log(err)
				})
			})
		},
		// 排队中取消状态
		tongzhi: function(id) {
			var that = this
			uni.request({
				url: this.$bashUrl + '/index.php/home/Api/store_linetype/',
				method: 'POST',
				data: {
					id: id,
					type: 1,
				},
				success: (res => {
					uni.showToast({
						title: '通知成功'
					});
					this.a = []
					this.b = []
					this.c = []
					this.pageInit()
				}),
				fail: (err => {
					console.log(err)
				})
			})
		},
		quxiao: function(id) {
			var that = this
			uni.request({
				url: this.$bashUrl + '/index.php/home/Api/store_linetype/',
				method: 'POST',
				data: {
					id: id,
					type: 2,
				},
				success: (res => {
					uni.showToast({
						title: '取消成功',
						icon: 'none'
					})
					this.a = []
					this.b = []
					this.c = []
					this.pageInit()
				})
			})
		}
	}
}
